/*----------------------------------------*/
/*  01. Pagination
/*----------------------------------------*/
.woocommerce-pagination,
.nav-links {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 30px;
}
.woocommerce-pagination .page-numbers,
.nav-links .page-numbers {
  margin: 0 5px;
  width: 30px;
  height: 30px;
  background-color: white;
  color: var(--title);
  text-decoration: none;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s ease, color 0.3s ease;
  cursor: pointer;
}
.woocommerce-pagination .page-numbers.current,
.nav-links .page-numbers.current {
  background-color: var(--primary);
  color: white;
  border-color: var(--primary);
}
.woocommerce-pagination .prev.page-numbers, .woocommerce-pagination .next.page-numbers,
.nav-links .prev.page-numbers,
.nav-links .next.page-numbers {
  border-radius: 5px;
  text-decoration: none;
  margin-left: 5px;
  white-space: nowrap;
}

/*----------------------------------------*/
/*  01. Footer Top
/*----------------------------------------*/
.footer-top {
  background-color: var(--white);
}
@media (max-width: 781px) {
  .footer-top .footer-top__container .wp-block-columns {
    gap: 0;
  }
}
@media (max-width: 781px) {
  .footer-top .footer-top__container .wp-block-column {
    flex-basis: 50% !important;
    flex-grow: unset;
  }
}
.footer-top .footer-top__item {
  text-align: center;
}
.footer-top svg {
  width: 52px;
  height: 52px;
}
.footer-top svg svg {
  width: 52px;
  height: 52px;
}
.footer-top h3 {
  font-size: 16px;
  font-weight: 600;
  line-height: 1.3em;
  margin-bottom: 8px;
  color: #000000;
}
.footer-top p {
  color: #979797;
  font-size: 14px;
  line-height: 1.6em;
  margin: 0;
}

/*----------------------------------------*/
/*  02. Popular-Search
/*----------------------------------------*/
.popular-search__container > div {
  border-top: 1px solid var(--border);
  padding-top: 20px;
}
.popular-search h3 {
  color: #000000;
  font-size: 16px;
  font-weight: 600;
}
.popular-search a {
  font-size: 12px;
  line-height: 16px;
  border-radius: 6px;
  border: 1px solid #DBDBDB;
  padding: 6px 12px;
  transition: all 0.3s;
  display: inline-flex;
  margin-right: 10px;
  margin-bottom: 10px;
}
.popular-search ul {
  display: flex;
  flex-flow: row wrap;
}
.popular-search ul li:first-child {
  display: none;
}

/*----------------------------------------*/
/*  03. Footer Main
/*----------------------------------------*/
.site-footer {
  padding: 30px 0;
  background-color: #F7F7F7;
  color: #666666;
  font-size: 14px;
}
.site-footer .is-layout-flex {
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 0;
  margin: 0;
}
@media (min-width: 588px) {
  .site-footer .is-layout-flex {
    flex-wrap: unset;
    flex-flow: row wrap;
  }
}
@media (min-width: 1024px) {
  .site-footer .is-layout-flex {
    flex-wrap: nowrap;
    gap: 20px;
  }
}
@media (max-width: 781px) {
  .site-footer .wp-block-columns .wp-block-column:nth-child(1) {
    flex-basis: 50% !important;
    margin-bottom: 20px;
  }
}
@media (max-width: 575px) {
  .site-footer .wp-block-columns .wp-block-column:nth-child(1) {
    flex-basis: 100% !important;
  }
}
@media (max-width: 781px) {
  .site-footer .wp-block-columns .wp-block-column:nth-child(2) {
    flex-basis: 50% !important;
    margin-bottom: 20px;
  }
}
@media (max-width: 575px) {
  .site-footer .wp-block-columns .wp-block-column:nth-child(2) {
    flex-basis: 100% !important;
  }
}
@media (max-width: 781px) {
  .site-footer .wp-block-columns .wp-block-column:nth-child(3) {
    flex-basis: 50% !important;
    margin-bottom: 20px;
  }
}
@media (max-width: 575px) {
  .site-footer .wp-block-columns .wp-block-column:nth-child(3) {
    flex-basis: 100% !important;
  }
}
@media (max-width: 781px) {
  .site-footer .wp-block-columns .wp-block-column:nth-child(4) {
    flex-basis: 50% !important;
  }
}
@media (max-width: 575px) {
  .site-footer .wp-block-columns .wp-block-column:nth-child(4) {
    flex-basis: 100% !important;
  }
}
.site-footer .wp-block-heading {
  color: #333;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: bold;
}
.site-footer .wp-block-list li a {
  color: var(--title);
}
.site-footer .wp-block-list li a:hover {
  color: var(--title);
}
.site-footer .wp-block-list li:not(:last-child) {
  margin-bottom: 20px;
}

/*----------------------------------------*/
/*  04. Footer Absolute
/*----------------------------------------*/
.footer-absolute {
  background-color: var(--primary);
  color: var(--black);
  padding: 5px 0;
}
@media (max-width: 991px) {
  .footer-absolute {
    margin-bottom: 60px;
  }
}
.footer-absolute .footer-absolute__container {
  border-top: 1px solid #333;
  padding: 20px 0;
}
.footer-absolute p {
  font-size: 12px;
}

/*----------------------------------------*/
/*  05. Footer Info Item
/*----------------------------------------*/
.footer__info-item div {
  display: flex;
  align-items: center;
}
.footer__info-item div svg {
  margin-right: 10px;
}

/*----------------------------------------*/
/*  06. Footer Logo
/*----------------------------------------*/
.footer--logo img {
  width: 280px;
  height: auto;
}

.footer-about__heading {
  margin: 10px 0;
}

.footer-about__hotline {
  margin-bottom: 10px;
}

/*----------------------------------------*/
/*  07. Toggle Menu Mobile
/*----------------------------------------*/
.toggle-menu-mobile {
  width: 30px;
  display: none;
}
.toggle-menu-mobile:hover {
  cursor: pointer;
}
.toggle-menu-mobile:hover .line {
  -webkit-transform: rotate(-30deg);
  -ms-transform: rotate(-30deg);
  -o-transform: rotate(-30deg);
  -moz-transform: rotate(-30deg);
  transform: rotate(-30deg);
}
.toggle-menu-mobile:hover .line:nth-child(1) {
  width: 10px;
}
.toggle-menu-mobile:hover .line:nth-child(2) {
  width: 20px;
}
.toggle-menu-mobile .line {
  width: 25px;
  height: 3px;
  background-color: var(--black);
  display: block;
  margin: 5px auto;
  transition: 0.3s ease-in-out;
}

/*----------------------------------------*/
/*  08. Sticky Header
/*----------------------------------------*/
@media (min-width: 1024px) {
  header.header--visible {
    position: fixed;
    left: 0;
    width: 100%;
    top: 0;
    background-color: black;
    color: white;
    animation: 300ms ease-in-out 0s 1 normal none running fadeInDown;
    box-shadow: rgba(8, 0, 42, 0.08) 0px 10px 20px 0px;
    z-index: 99;
  }
}
header.header--visible .header__nav {
  margin-top: 0;
}
header.header--visible .header__row {
  padding-top: 0;
}
header.header--visible .buy-online__wrapper {
  padding: 6px 20px 6px 20px;
}
header.header--visible .buy-online__wrapper h2 {
  display: none;
}
header.header--visible .buy-online__wrapper svg {
  width: 24px;
  height: 24px;
}
header.header--visible .buy-online__wrapper svg svg {
  width: 24px;
  height: 24px;
}
header.header--visible .header__bottom-container {
  max-width: 100%;
  padding: 0;
  overflow-x: auto;
  white-space: nowrap;
}
header.header--visible .category-grid {
  margin: 0;
}
header.header--visible .category-grid__wrapper {
  border-radius: 0;
  padding: 5px;
}
header.header--visible .category-grid__item a {
  display: flex;
  align-items: center;
  font-weight: bold;
}
header.header--visible .image {
  width: 20px;
  height: 20px;
}
header.header--visible .image__img {
  height: auto;
  max-width: 100%;
}

/*----------------------------------------*/
/*  09. Comment
/*----------------------------------------*/
#commentform {
  display: flex;
  flex-direction: column;
  flex-flow: row wrap;
  justify-content: space-between;
}

#commentform span {
  color: var(--title);
}

#commentform .textarea-field {
  order: 2;
}

#commentform .form-submit {
  order: 3;
}

#commentform .form-submit .submit {
  background: var(--primary);
  color: var(--black);
  transition: background 0.3s ease, color 0.3s ease;
  padding: 0.625em 1.25em;
  border: 0;
}
@media (min-width: 1024px) {
  #commentform .form-submit .submit:hover {
    color: white;
    background: var(--black);
  }
}

#commentform .name-email-field {
  margin-bottom: 1rem;
}

#commentform .name-email-field .field-item {
  width: 49%;
}

#commentform input:not([type=checkbox], [type=submit]),
#commentform textarea {
  border: 1px solid #f0f0f0;
  border-radius: 8px;
  color: var(--title);
  width: 100%;
  padding: 1rem;
  outline: none;
  box-shadow: none;
}

#commentform input:hover,
#commentform input:active,
#commentform textarea:hover,
#commentform textarea:active {
  outline: none;
  box-shadow: none;
}

#commentform input::placeholder,
#commentform textarea::placeholder {
  font-size: 14px;
  line-height: 21px;
  color: var(--title);
}

#commentform label {
  color: var(--title);
  margin-bottom: 0.5rem;
}

#commentform textarea {
  height: 100px;
}

#commentform button {
  margin-top: 0.5rem;
  padding: 0 1.5rem;
}

.form-title {
  font-size: 20px;
  line-height: 30px;
  font-weight: 600;
  color: var(--gray-700);
}

@media (min-width: 992px) {
  .form-title {
    font-size: 30px;
    line-height: 45px;
  }
}
.comments-area.no-comment .form-title {
  display: none;
}

.comments-area.no-comment .form-title.no-comment {
  display: block;
}

.comments-title {
  text-transform: uppercase;
}

.comment-list-wrap {
  margin-bottom: 40px;
}

.comment-list-wrap .comment-list {
  padding-left: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}

.comment-list-wrap .comment-list li {
  list-style: none !important;
}

.comment-list-wrap .comment-list li.bypostauthor {
  border: 0;
  padding-left: 0;
}

.comment-list-wrap .comment-list .comment-respond {
  margin-bottom: 50px;
}

.comment-list-wrap .comment-list li .comment-body {
  position: relative;
  display: flex;
}

.comment-list-wrap .comment-list li:not(:last-child) .comment-body {
  padding-bottom: 38px;
}

@media (max-width: 575.98px) {
  .comment-list-wrap .comment-list .comment-body {
    padding-bottom: 15px;
    margin-bottom: 15px;
  }
}
.comment-list-wrap .comment-list .comment-body + .children .comment-body {
  padding-bottom: 25px;
}

@media (max-width: 575.98px) {
  .comment-list-wrap .comment-list .comment-body .comment-author {
    width: 40px;
    margin-right: 10px;
  }
}
.comment-list-wrap .comment-list .comment-body .comment-author .avatar {
  border-radius: 50%;
  width: 60px;
  height: 60px;
}

.comment-list-wrap .comment-list .comment-body .comment-details {
  width: calc(100% - 60px);
  padding-left: 10px;
}

@media (max-width: 575.98px) {
  .comment-list-wrap .comment-list .comment-body .comment-details {
    width: calc(100% - 50px);
  }
}
.comment-list-wrap .comment-list .comment-body .comment-details .comment-edit-link {
  display: none;
}

.comment-list-wrap .comment-list .comment-body .comment-details .top {
  display: flex;
  margin-bottom: 8px;
  align-items: center;
  padding-top: 0px;
  margin-top: -6px;
}

.comment-list-wrap .comment-list .comment-body .comment-details .top .says {
  display: none;
}

.comment-list-wrap .comment-list .comment-body .comment-details .top .fn,
.comment-list-wrap .comment-list .comment-body .comment-details .top .url {
  font-size: 16px;
  line-height: 24px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  color: var(--title);
  text-transform: capitalize;
}

.comment-list-wrap .comment-list .comment-body .comment-details .top .date-time {
  font-size: 14px;
  line-height: 21px;
  text-align: left;
  color: var(--title);
  margin-left: 12px;
}

.comment-list-wrap .comment-list .comment-body .comment-details .top .reply a {
  font-size: 14px;
  line-height: 21px;
  text-align: left;
  color: var(--red-500);
  text-transform: capitalize;
  text-decoration: underline;
}

.comment-list-wrap .comment-list .comment-body .comment-details .top .separator {
  font-size: 14px;
  line-height: 21px;
  margin: 0 5px;
}

@media (min-width: 992px) {
  .comment-list-wrap .comment-list .comment-body .comment-details .top .separator {
    font-size: 16px;
    line-height: 24px;
  }
}
@media (max-width: 375px) {
  .comment-list-wrap .comment-list .comment-body .comment-details .top {
    display: block;
    position: relative;
  }
  .comment-list-wrap .comment-list .comment-body .comment-details .top .date-time {
    width: 100%;
    display: block;
    margin-left: 0;
  }
  .comment-list-wrap .comment-list .comment-body .comment-details .top .separator {
    display: none;
  }
  .comment-list-wrap .comment-list .comment-body .comment-details .top .reply {
    position: absolute;
    top: 0;
    right: 0;
  }
}
.comment-list-wrap .comment-list .comment-body .comment-details .comment-text p {
  font-size: 16px;
  line-height: 24px;
  color: var(--gray-700);
}

.comment-list-wrap .comment-list .comment-body .reply {
  margin-bottom: 0;
}

.comment-list-wrap .comment-list .comment-body .reply a {
  font-size: 12px;
  line-height: 18px;
  text-transform: uppercase;
  font-weight: 500;
}

.comment-list-wrap .comment-list .comment-meta {
  margin-bottom: 5px;
}

.comment-list-wrap .comment-list .comment-meta:after {
  content: "";
  display: table;
  clear: both;
}

.comment-list-wrap .comment-list .comment-content {
  padding-left: 130px;
  margin-top: -45px;
}

@media (max-width: 575.98px) {
  .comment-list-wrap .comment-list .comment-content {
    padding-left: 0;
    margin-top: 0;
  }
}
.comment-list-wrap .comment-list .comment-content p {
  margin-bottom: 25px;
  line-height: 24px;
}

.comment-list-wrap .comment-list .edit-link {
  margin-left: 10px;
}

.comment-list-wrap .comment-list .edit-link a {
  color: var(--gray-700);
  font-size: 14px;
  line-height: 21px;
}

.comment-list-wrap .comment-list .reply {
  text-transform: uppercase;
  margin-bottom: 30px;
}

.comment-list-wrap .comment-list > .comment:first-child {
  border-top: none;
}

.comment-list-wrap .comment-list ol {
  list-style-type: none;
}

@media (min-width: 768px) {
  .comment-list-wrap .comment-list ol {
    padding-left: 70px;
  }
}
@media (max-width: 575.98px) {
  .comment-list-wrap .comment-list ol {
    padding-left: 15px;
  }
}
#reply-title,
.comment-notes,
.heading-count,
#commentform .comment-form-url {
  display: none;
}

#commentform .comment-form-comment {
  width: 100%;
}

#commentform .comment-form-author {
  width: 49%;
  margin-bottom: 10px;
}

#commentform .comment-form-email {
  width: 49%;
  margin-bottom: 10px;
}

@media (max-width: 768px) {
  #commentform .comment-form-author {
    width: 100%;
  }
  #commentform .comment-form-email {
    width: 100%;
  }
}
#commentform .comment-form-cookies-consent {
  display: none;
}

.comment-respond {
  margin-top: 20px;
}

/*----------------------------------------*/
/* 11. Page 404
/*----------------------------------------*/
.not-found-page {
  padding: 50px 20px;
  text-align: center;
  background-color: #f9f9f9;
  display: flex;
  justify-content: center;
  align-items: center;
}

.not-found-content {
  max-width: 600px;
  margin: auto;
}

.not-found-title {
  font-size: 8rem;
  font-weight: 800;
  color: var(--primary);
  margin-bottom: 20px;
}

.not-found-message {
  font-size: 1.25rem;
  color: #555;
  margin-bottom: 30px;
}

.not-found-btn {
  display: inline-block;
  padding: 12px 30px;
  font-size: 1rem;
  color: #fff;
  background-color: var(--primary);
  text-decoration: none;
  border-radius: 6px;
  transition: background-color 0.3s ease;
}

.not-found-btn:hover {
  background-color: var(--primary);
  color: white;
}

.main-menu nav {
  position: relative !important;
  z-index: 9;
}
.main-menu nav > .container {
  position: relative;
}
.main-menu nav .inside-navigation {
  position: static !important;
}

.main-menu nav {
  position: relative !important;
  width: 100%;
}
.main-menu nav > ul li.has-mega-menu {
  position: static;
  display: inline-flex;
  align-items: center;
  margin-right: 0.75em;
}
.main-menu nav > ul li.has-mega-menu a.btn {
  color: var(--bs-primary);
  line-height: 24px;
  font-weight: 500;
  padding: 0;
  border: 0;
}
.main-menu nav > ul li.has-mega-menu a.btn .icon {
  color: var(--bs-primary);
  transform: none;
}
.main-menu nav > ul li.has-mega-menu .icon {
  width: 20px;
  height: 20px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  color: var(--bs-white);
  transform: translateX(-1em);
}
.main-menu nav > ul li.has-mega-menu .icon svg {
  width: 20px;
  height: 20px;
}
.main-menu nav > ul li.has-mega-menu .submenu {
  display: none;
}
.main-menu nav > ul li.has-mega-menu .sub-menu {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: auto;
  padding: 1em;
  background: var(--bs-light);
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.4s ease-in-out;
  z-index: 999;
  margin-top: -1px;
  box-shadow: 0 3px 7px 1px rgba(0, 0, 0, 0.3), 0 6px 12px -2px rgba(50, 50, 93, 0.25);
}
.main-menu nav > ul li.has-mega-menu .sub-menu .wp-block-columns {
  margin: 0;
}
.main-menu nav > ul li.has-mega-menu .sub-menu p {
  line-height: 1.25;
}
.main-menu nav > ul li.has-mega-menu .sub-menu .wp-block-heading {
  padding: 0;
  color: var(--bs-dark);
  font-weight: 700;
  line-height: 1.5;
}
.main-menu nav > ul li.has-mega-menu .sub-menu .row {
  margin: 0;
}
.main-menu nav > ul li.has-mega-menu .sub-menu .row > * {
  position: relative;
  padding: 0 0 0 1em;
}
.main-menu nav > ul li.has-mega-menu .sub-menu .row > *::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 1px;
  background: rgba(0, 0, 0, 0.25);
  height: 100%;
}
.main-menu nav > ul li.has-mega-menu .sub-menu .row > *:first-child::before {
  content: none;
}
.main-menu nav > ul li.has-mega-menu .sub-menu ul {
  margin: 0;
  padding: 0;
  text-indent: 0;
  list-style-type: none;
  position: static;
  float: none;
  width: auto;
  height: auto;
  background: none;
  box-shadow: none;
  opacity: 1;
  display: flex;
  flex-direction: column;
}
.main-menu nav > ul li.has-mega-menu .sub-menu ul li {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 30px;
}
.main-menu nav > ul li.has-mega-menu .sub-menu ul li a {
  padding: 0.25em 0;
  color: var(--black);
  font-weight: 500;
  font-size: 14px;
  text-transform: capitalize;
  text-decoration: none;
}
.main-menu nav > ul li.has-mega-menu .sub-menu ul li a:hover {
  color: var(--primary);
}
