/*----------------------------------------*/
/*  03. SHOW MORE / SHOW LESS
/*----------------------------------------*/
.js-content-toggle.has-toggle .single__content {
  max-height: 1380px;
  overflow: hidden;
  position: relative;
}
.js-content-toggle.has-toggle .single__content::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  bottom: 0;
  z-index: 1;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(50%, rgba(255, 255, 255, 0)), to(white));
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 50%, white 100%);
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 50%, white 100%);
  pointer-events: none;
}
.js-content-toggle.has-toggle .show-more {
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--title);
  background: var(--primary);
  color: var(--black);
  transition: background 0.3s ease, color 0.3s ease;
  height: 36px;
  width: 150px;
  border-radius: 0;
}
@media (min-width: 1024px) {
  .js-content-toggle.has-toggle .show-more:hover {
    color: white;
    background: var(--black);
  }
}
.js-content-toggle.has-toggle .show-more svg path {
  stroke: var(--black);
}
.js-content-toggle.has-toggle .show-more:hover svg path {
  stroke: var(--white);
}
.js-content-toggle.has-toggle .show-less {
  display: none;
  color: var(--title);
  background: var(--primary);
  color: var(--black);
  transition: background 0.3s ease, color 0.3s ease;
  height: 36px;
  width: 150px;
}
@media (min-width: 1024px) {
  .js-content-toggle.has-toggle .show-less:hover {
    color: white;
    background: var(--black);
  }
}
.js-content-toggle.has-toggle .show-less svg path {
  stroke: var(--white);
}

.js-content-toggle.has-toggle.is-expanded .single__content {
  max-height: none;
}
.js-content-toggle.has-toggle.is-expanded .single__content::before {
  content: none;
}
.js-content-toggle.has-toggle.is-expanded .show-more {
  display: none;
}
.js-content-toggle.has-toggle.is-expanded .show-less {
  display: flex;
  align-items: center;
  justify-content: center;
}

.js-btn-toggle-content {
  font-weight: bold;
  margin-top: 16px;
  display: flex;
